html {
	font-family: sans-serif;
	line-height: 1.15;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

body {
	margin: 0;
	padding: 0;
}

article, aside, footer, header, nav, section {
	display: block;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

figcaption, figure, main {
	display: block;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
	overflow: visible;
}

pre {
	font-family: monospace, monospace;
	font-size: 1em;
}

a {
	background-color: transparent;
	-webkit-text-decoration-skip: objects;
}

a:active, a:hover {
	outline-width: 0;
}

abbr[title] {
	border-bottom: none;
	text-decoration: underline;
	text-decoration: underline dotted;
}

b, strong {
	font-weight: inherit;
}

b, strong {
	font-weight: bolder;
}

code, kbd, samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

dfn {
	font-style: italic;
}

mark {
	background-color: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub, sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}

audio, video {
	display: inline-block;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

img {
	border-style: none;
}

svg:not(:root) {
	overflow: hidden;
}

button, input, optgroup, select, textarea {
	font-family: sans-serif;
	font-size: 100%;
	line-height: 1.15;
	margin: 0;
}

button, input {
	overflow: visible;
}

button, select {
	text-transform: none;
}

button, html [type="button"], [type="reset"], [type="submit"] {
	-webkit-appearance: button;
}

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
	border-style: none;
	padding: 0;
}

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
	outline: 1px dotted ButtonText;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	box-sizing: border-box;
	color: inherit;
	display: table;
	max-width: 100%;
	padding: 0;
	white-space: normal;
}

progress {
	display: inline-block;
	vertical-align: baseline;
}

textarea {
	overflow: auto;
}

[type="checkbox"], [type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
	height: auto;
}

[type="search"] {
	-webkit-appearance: textfield;
	outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

::-webkit-file-upload-button {
	-webkit-appearance: button;
	font: inherit;
}

details, menu {
	display: block;
}

summary {
	display: list-item;
}

canvas {
	display: inline-block;
}

template, [hidden] {
	display: none;
}

.icon {
	display: inline-block;
	width: 1em;
	height: 1em;
	fill: #000;
}

.icon-facebook-logo {
	font-size: 51.2rem;
	width: 1em;
}

.icon-google-plus {
	font-size: 51.2rem;
	width: 1em;
}

.icon-twitter {
	font-size: 51.2rem;
	width: 1em;
}

html {
	box-sizing: border-box;
}

*, *::before, *::after {
	box-sizing: inherit;
}

body {
	font-family: Georgia, Arial, sans-serif;
	font-size: 16px;
	line-height: 1;
	color: #000;
	overflow-x: hidden;
	background-color: #fff;
}

::selection {
	background: #b3d4fc;
	text-shadow: none;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

h1, h2, h3, h4, h5, h6 {
	margin: 0;
}

p {
	margin: 0;
}

ul, ol {
	margin: 0;
	padding: 0;
}

ul li {
	list-style: none;
}

textarea {
	resize: none;
}

body {
	background: #e0e0e0;
}

.container {
	max-width: 960px;
	width: 100%;
	margin: 0 auto;
}

@media only screen and (max-width: 980px) {
	.container {
		padding: 0 10px;
	}
}

.header {
	height: 52px;
	background: #2d2928;
}

@media only screen and (max-width: 470px) {
	.header {
		height: 70px;
	}
}

.header > .container {
	height: inherit;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

@media only screen and (max-width: 470px) {
	.header > .container {
		flex-direction: column;
		justify-content: center;
	}
}

.links {
	display: flex;
}

@media only screen and (max-width: 470px) {
	.links {
		margin-bottom: 10px;
	}
}

.links__head {
	text-decoration: none;
	text-transform: capitalize;
	font-size: 12px;
	color: #c4c4c4;
	margin-right: 12px;
	position: relative;
	transition: .25s ease;
}

.links__head:hover {
	color: #fff;
}

.links__head::after {
	position: absolute;
	content: '|';
	top: 0;
	right: -8px;
}

.links__head:last-child {
	margin-right: 60px;
}

@media only screen and (max-width: 470px) {
	.links__head:last-child {
		margin-right: 0;
	}
}

.links__head:last-child::after {
	content: '';
}

.social {
	display: flex;
}

.social__link {
	display: bloc;
	width: 32px;
	height: 32px;
	background: #fff;
	border-radius: 50%;
	margin-right: 10px;
	line-height: 40px;
	text-align: center;
	transition: .75s ease;
}

.social__link:last-child {
	margin-right: 0;
}

.social__link:hover {
	background: #000;
}

.social__link:hover svg {
	fill: #fff;
}

.social__link svg {
	font-size: 18px;
	fill: #000;
	transition: .75s ease;
}

.title-section {
	text-align: center;
}

.title-section__title {
	font-size: 100px;
	line-height: 1.2;
	color: #282828;
	margin-bottom: 18px;
}

@media only screen and (max-width: 974px) {
	.title-section__title {
		font-size: 50px;
		margin-bottom: 0;
	}
}

@media only screen and (max-width: 500px) {
	.title-section__title {
		font-size: 24px;
	}
}

.title-section__description {
	font-size: 20px;
	color: #4a4a4a;
	margin-bottom: 30px;
}

@media only screen and (max-width: 500px) {
	.title-section__description {
		margin-bottom: 10px;
		font-size: 14px;
	}
}

.navigation {
	display: flex;
	position: relative;
	margin-bottom: 44px;
}

@media only screen and (max-width: 670px) {
	.navigation {
		flex-direction: column;
		align-items: center;
		margin-bottom: 20px;
	}
}

.navigation__link {
	display: block;
	text-decoration: none;
	text-transform: uppercase;
	color: #333;
	margin-right: 36px;
	font-size: 14px;
	transition: .5s ease;
	line-height: 30px;
}

.navigation__link:hover {
	color: #999;
}

.navigation .is-active {
	color: #999;
}

.navigation:before {
	position: absolute;
	top: -1px;
	width: 100%;
	height: 3px;
	content: '';
	background: url(../img/line.png) repeat-x;
}

.navigation:after {
	position: absolute;
	top: 27px;
	width: 100%;
	height: 3px;
	content: '';
	background: url(../img/line.png) repeat-x;
}

@media only screen and (max-width: 670px) {
	.navigation:after {
		top: 100%;
	}
}

.main > .container {
	display: flex;
	justify-content: space-between;
}

@media only screen and (max-width: 960px) {
	.main > .container {
		flex-direction: column;
	}
}

.main__news {
	width: 55%;
}

.main__subnews {
	flex: 1;
}

a:active, a:hover {
	outline-width: auto;
}

.news a {
	text-decoration: none;
	color: inherit;
}

.news_big {
	margin-bottom: 20px;
}

@media only screen and (max-width: 960px) {
	.news_big {
		width: 100%;
	}
}

.news_medium {
	margin-bottom: 16px;
}

.news_medium::after {
	content: "";
	display: block;
	clear: both;
}

.news_small {
	margin-bottom: 22px;
	position: relative;
}

.news_small::after {
	content: "";
	display: block;
	clear: both;
}

.g-news__title {
	font-size: 22px;
	color: #444;
	line-height: 1;
	margin-bottom: 8px;
}

.news__title_big {
	max-width: 500px;
}

@media only screen and (max-width: 960px) {
	.news__title_big {
		width: 100%;
	}
}

.news__title_small {
	font-size: 12px;
}

.news__text_big {
	max-width: 500px;
}

@media only screen and (max-width: 960px) {
	.news__text_big {
		max-width: 100%;
	}
}

.g-news__text {
	font-size: 16px;
	line-height: 1.2;
	color: #666;
}

.g-news__text_small {
	font-size: 12px;
}

.news__image {
	display: inline-block;
}

.news__image_big {
	width: 500px;
	height: 320px;
	background-size: cover;
	margin-bottom: 16px;
}

@media only screen and (max-width: 500px) {
	.news__image_big {
		width: 320px;
	}
}

.news__image_medium {
	width: 100px;
	height: 100px;
	float: left;
	margin: 4px;
	margin-right: 18px;
	outline: 1px solid #999;
	outline-offset: 4px;
}

.news__image_small {
	width: 60px;
	height: 60px;
	float: left;
	margin: 4px;
	margin-right: 12px;
	outline: 1px solid #999;
	outline-offset: 4px;
}

.news__image_item {
	display: block;
	width: 122px;
	height: 80px;
	margin: 4px;
	margin-bottom: 12px;
	outline: 1px solid #999;
	outline-offset: 4px;
}

.content {
	padding: 40px 0 30px;
	display: flex;
	justify-content: space-between;
	position: relative;
}

@media only screen and (max-width: 960px) {
	.content {
		flex-direction: column;
	}
}

.content:before {
	position: absolute;
	top: 11px;
	content: '';
	height: 3px;
	width: 100%;
	background: url(../img/line.png) repeat-x;
}

.content__block-inner {
	width: 95%;
	display: flex;
	justify-content: space-between;
	padding-bottom: 40px;
	margin-bottom: 20px;
	position: relative;
}

.content__block-inner:after {
	position: absolute;
	bottom: 8px;
	content: '';
	height: 3px;
	width: 100%;
	background: url(../img/line.png) repeat-x;
}

.content__link {
	width: 34%;
	margin-left: 30px;
}

.content__inner-block {
	width: 48.3%;
}

.content__title {
	padding-bottom: 8px;
	margin-bottom: 18px;
	border-bottom: 1px solid #666;
	font-size: 16px;
	color: #666;
}

.g-read {
	color: #982115 !important;
	font-size: 11px;
	font-weight: 400;
	line-height: 11px;
	transition: .5s ease;
}

.g-read:hover {
	color: red !important;
}

.g-read_more {
	text-transform: uppercase;
	position: absolute;
	bottom: -14px;
	right: 15px;
}

.g-read_tag {
	font-size: 11px;
	color: #982014;
	text-transform: capitalize;
	text-decoration: none;
}

.news-menu {
	display: flex;
	margin-bottom: 20px;
}

.news-menu__item {
	background: #2d2928;
	margin-right: 1px;
}

.news-menu__item.is-active .news-menu__link {
	color: silver;
}

.news-menu__link {
	display: block;
	font-size: 14px;
	color: #fff;
	text-decoration: none;
	padding: 14px 14px;
	transition: .5s ease;
}

@media only screen and (max-width: 960px) {
	.news-menu__link {
		text-align: center;
	}
}

.news-menu__link:hover {
	color: silver;
}

.news-items {
	display: flex;
	flex-flow: row wrap;
}

@media only screen and (max-width: 960px) {
	.news-items {
		justify-content: space-around;
	}
}

.news-item {
	max-width: 130px;
	margin-right: 30px;
	margin-bottom: 10px;
}

@media only screen and (max-width: 960px) {
	.news-item {
		width: 20%;
		margin-right: 0;
	}
}

@media only screen and (max-width: 590px) {
	.news-item {
		width: 50%;
	}
}

.news-item:last-child {
	margin-right: 0;
}

.news-item__title {
	display: block;
	color: #982115;
	font-size: 12px;
	line-height: 1.2;
	margin-bottom: 8px;
	cursor: pointer;
}

.news-item__text {
	font-size: 12px;
	line-height: 1.2;
	color: #666;
	margin-bottom: 12px;
}

.news-item__tag {
	font-size: 12px;
	color: #666;
}

.content__links {
	display: flex;
	width: 64%;
	justify-content: space-between;
}

@media only screen and (max-width: 960px) {
	.content__links {
		width: 100%;
		justify-content: space-around;
	}
}

.content__dvertisement {
	width: 43%;
}

.content__set-links {
	width: 43%;
}

.advertisement__img {
	display: block;
	width: 160px;
	height: 600px;
}

.g-advertisement__title {
	display: block;
	font-size: 14px;
	color: #444;
	padding-top: 8px;
	border-top: 5px solid #999;
	margin-bottom: 16px;
	text-decoration: none;
}

.g-link {
	display: block;
	color: #982216;
	text-decoration: none;
	font-size: 13px;
	margin-bottom: 10px;
	transition: .5s ease;
}

.g-link:hover {
	color: #ff3333;
}

.categories__list:last-child {
	margin-bottom: 15px;
}

.additional-links {
	padding: 36px 0;
}

.additional-links > .container {
	display: flex;
	justify-content: space-between;
	position: relative;
	flex-wrap: wrap;
}

@media only screen and (max-width: 600px) {
	.additional-links > .container {
		justify-content: center;
		align-items: center;
	}
}

.additional-links > .container:before {
	position: absolute;
	top: -36px;
	content: '';
	height: 3px;
	width: 100%;
	background: url(../img/line.png) repeat-x;
}

.additional-links > .container:after {
	position: absolute;
	bottom: -36px;
	content: '';
	height: 3px;
	width: 100%;
	background: url(../img/line.png) repeat-x;
}

@media only screen and (max-width: 600px) {
	.additional-links__item {
		width: 50%;
		text-align: center;
	}
}

.footer {
	padding: 30px 0;
}

.footer__copyright {
	color: #787878;
	font-size: 12px;
}
